body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
textarea {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  width: 200px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  display: flex;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  width: 300px;
  height: 300px;
  background-color: #fee2e2;
}

input {
  width: 200px;
}

form > div {
  display: flex;
  flex-direction: column;
}

.buttons {
  display: flex;
  flex-direction: row;
  grid-gap: 10px;
  gap: 10px;
  padding: 20px;
}

.container {
  display: flex;

  border: 1px solid black;
}

.CV-Template {
  border: 1px solid black;
}

#name {
  font-size: 36px;
  font-weight: 800;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Education {
  padding: 10px 50px 20px;
  grid-gap: 15px;
  gap: 15px;
  display: flex;
  flex-direction: column;
}

.college > *,
.major > *,
.date > *,
.company > *,
.job > *,
.job-description > * {
  margin: 0;
}

.CV-Template {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.General {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
}

.name {
  font-size: 26px;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

.test {
  display: flex;
  justify-content: center;
  font-size: 20px;
  grid-gap: 15px;
  gap: 15px;
}

hr {
  border: none;
  border-top: 3px double #333;
  color: #333;
  overflow: visible;
  text-align: center;
  height: 5px;
  width: 700px;
}

.Work {
  padding: 10px 50px 20px;
  grid-gap: 15px;
  gap: 15px;
  display: flex;
  flex-direction: column;
}

